import React, { useState } from 'react';
import './App.scss';
import { MoviesList } from './components/MoviesList';
import moviesFromServer from './api/movies.json';

export const App: React.FC = () => {
  const [query, setQuery] = useState('');

  const movieCheck = (movie: Movie) => {
    const movieTitleCheck = movie.title.toLowerCase()
      .includes(query.toLowerCase());
    const movieDescriptionCheck = movie.description.toLowerCase()
      .includes(query.toLowerCase());

    return movieTitleCheck || movieDescriptionCheck;
  };

  const visibleMovies = moviesFromServer
    .filter(movie => movieCheck(movie));

  const queryHandler = (
    event: React.ChangeEvent<HTMLInputElement>,
  ) => setQuery(event.target.value);

  return (
    <div className="page">
      <div className="page-content">
        <div className="box">
          <div className="field">
            <label
              htmlFor="search-query"
              className="label"
              aria-label="Search movie"
            >
              Search movie
            </label>

            <div className="control">
              <input
                type="text"
                id="search-query"
                className="input"
                placeholder="Type search word"
                value={query}
                onChange={queryHandler}
              />
            </div>
          </div>
        </div>

        <MoviesList movies={visibleMovies} />
      </div>

      <div className="sidebar">
        Sidebar goes here
      </div>
    </div>
  );
};
